.boy-picture {
    width: 254px;
    height: 253px;
    margin-top: 30px;
    border-radius: 5px;
    background: #ebebeb;
    box-shadow: 0px 40px 50px -30px rgba(36, 36, 113, 0.5);
  }
  .slide2 {
    background-image: url("../coding-bootcamp-testimonials-slider-master/images/pattern-bg.svg");
    display: flex;
    justify-content: center;
    background-size: 100%;
    background-repeat: no-repeat;
    width: 326.5px;
    height: 307.5px;
  }
  .info2 {
    background-image: url("../coding-bootcamp-testimonials-slider-master/images/pattern-quotes.svg");
    background-repeat: no-repeat;
    background-position: center 1%;
    background-size: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 312px;
    margin-top: 20px;
    text-align: center;
    color: #202047;
  }
  .txt2-h3 {
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
  }
  .txt2-h4 {
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    margin-top: -4px;
  }
  .txt2-p {
    margin-top: -20px;
  }
  